.breadcrumbs {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0 10px;
    a {
        display: flex;
        align-items: flex-start;
    }
    a::after {
        content: "\E019";
        display: inline-block;
        position: static;
        font-family: "icons";
        font-weight: 400;
        font-size: 4px;
        transform: rotate(-90deg);
        margin: 5px 7px;
    }
    span {
        color: #000;
    }
}